.checkout .progress-bar {
  flex-direction: row;
  background-color: transparent;
  width: calc(50% - 1rem);
  position: relative;
  transform: translateX(-0.5rem);
  overflow: visible;
}

.checkout .progress-bar::before {
  content: '';
  width: 100%;
  height: 1px;
  background: #a29b8d;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.checkout .progress-bar::after, .checkout .progress-bar li::before {
  content: '';
  display: block;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 1px solid #a29b8d;
  background-color: #5d533f;
  z-index: 2;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

@media (min-width: 768px) {
  .checkout .progress-bar::after, .checkout .progress-bar li::before {
    width: 1.25rem;
    height: 1.25rem;
  }
}

.checkout .progress-bar::after {
  right: 0;
  transform: translate(50%, -50%);
}

.checkout .progress-bar.active::after {
  background-color: #f0efea;
  border: 3px double #5d533f;
}

.checkout .progress-bar li {
  width: calc(100% / 2);
}

.checkout .progress-bar li.active::before {
  background-color: #f0efea;
  border: 3px double #5d533f;
}

.checkout .progress-bar li.done::before {
  background-color: #f0efea;
  content: '✓';
  font-size: 0.5rem;
  color: #5d533f;
}

.checkout .progress-bar li.done::after {
  content: '';
  background-color: #f0efea;
  width: 50%;
  z-index: 1;
  height: 1px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.checkout .progress-bar li.done:last-child::after {
  left: 50%;
}

